.App {
  height: 100vh;
  background-color: #000;
  color: #fff;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media (max-width: 480px) {
  .mobile-hidden {
    display: none;
  }
}